.App {
  text-align: center;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  overflow: visible;
}

body {
  /* overflow: hidden; */
  background-color: #23272A;
}

.horizontal-slider {
  width: 100%;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  padding: 0px;
  margin: 0px;
}

.vertical-slider {
  width: 100%;
  position: relative;
  overflow-y: hidden;
  padding: 0px;
  margin: 0px;
}

.horizontal-slider::-webkit-scrollbar {
  display: none;
}

.horizontal-slider {
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}